/* Base Container Styles */
.cruise-list-container {
  font-family: 'Arial', sans-serif;
  color: #333;
  max-width: 2000px; /* Maximum width of the content */
  margin: 0 auto; /* Centers the content */
  padding: 20px; /* Padding around the content */
  box-shadow: 0 2px 5px rgba(0,0,0,0.1); /* Optional: adds a shadow for some depth */
}

/* Header Styles */
header {
  background: #f0f8ff;
  padding: 1rem;
  text-align: center;
  border-radius: 8px; /* Optional: rounds the corners */
}

header h1 {
  margin: 0;
  color: #333;
  font-size: 2rem;
}

/* Navigation Tabs */
.cruise-list-container nav {
  display: flex;
  justify-content: center;
  padding: 1rem 0;
}

.cruise-list-container nav button {
  padding: 0.75rem 1.5rem;
  border: 1px solid #007bff;
  background-color: white;
  color: #007bff;
  margin: 0 10px;
  font-weight: 500;
  cursor: pointer;
  border-radius: 5px; /* Optional: rounds the corners */
  transition: all 0.3s ease;
}

.cruise-list-container nav button:hover,
.cruise-list-container nav button.active {
  background-color: #007bff;
  color: white;
}

/* Search and Filter Styles */
.search-filter-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px; /* Adjust this value based on your design preferences */
}

/* Keep the existing styles for table, input, select, and buttons */


.search-input,
.status-filter {
  padding: 10px;
  border: 1px solid #ced4da;
  border-radius: 5px;
  font-size: 1em;
}


/* Table Styles */
.cruise-list-table-container {
  overflow-x: auto; /* For responsive horizontal scrolling */
  margin-bottom: 20px; /* Space below the table */
}

.cruise-list-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem; /* Space above the table */
}

.cruise-list-table th,
.cruise-list-table td {
  padding: 0.75rem;
  border: 1px solid #ddd;
  text-align: left;
}

.cruise-list-table th {
  background-color: #f2f2f2;
}

/* Pagination Styles */
.pagination {
  display: flex;
  justify-content: center;
  padding: 1rem;
}

.page-number {
  padding: 0.5rem 1rem;
  margin: 0 5px;
  border: 1px solid #007bff;
  background-color: white;
  color: #007bff;
  cursor: pointer;
  transition: all 0.3s ease;
}

.page-number:hover,
.page-number.active {
  background-color: #007bff;
  color: white;
}

/* Footer Styles */
footer {
  background-color: #ececec;
  padding: 10px;
  text-align: center;
  border-radius: 8px; /* Optional: rounds the corners */
}

footer p {
  margin: 0;
}

footer a {
  color: #1a0dab;
  text-decoration: none;
}

/* Details Button Styles */
.details-button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 5px; /* Optional: rounds the corners */
  transition: background-color 0.3s;
}

.details-button:hover {
  background-color: #0056b3;
}

/* Responsive Styles */
@media (max-width: 600px) {
  .cruise-list-container {
    padding: 10px;
  }

  header h1 {
    font-size: 1.5rem;
  }

  .cruise-list-container nav button {
    padding: 0.5rem 1rem;
  }

  .search-filter-container {
    flex-direction: column;
    align-items: stretch;
  }

  .search-input,
  .status-filter {
    width: 100%;
    margin-bottom: 10px;
  }

  .pagination {
    flex-wrap: wrap;
  }

  .page-number {
    flex: 1 1 auto;
    margin: 3px;
    text-align: center;
  }
}


.tooltip-container {
  position: relative;
  display: inline-block;
}

.tooltip {
  visibility: hidden;
  width: 120px;
  background-color: black;  /* Ensure this is correct */
  color: white;  /* Ensuring text color is visible against the background */
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  /* Position the tooltip text */
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px; /* Use half of the width value to center the tooltip */

  /* Fade in tooltip */
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltip-container:hover .tooltip {
  visibility: visible;
  opacity: 1;
}
